@import "../Variables.scss";

.about {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  flex-flow: wrap;
  justify-content: space-evenly;
  margin-top: 60px;

  .about-img {
    margin: 20px;

    img {
      max-width: 400px;
      width: 100%;
      height: auto;
    }
  }

  .about-text {
    margin: 20px;
    span {
      font-size: $medium_font;
      color: $main_color;
      font-weight: bold;
      margin-bottom: 20px;
    }

    h1 {
      font-size: 1.5rem;
      font-weight: 400;
    }

    p {
      line-height: 1.5rem;
      font-size: 1.3rem;
    }

    button {
      background-color: $main_color;
      color: $text_color;
      padding: 10px 18px;
      margin: 10px 0;
      border-radius: 4px;
      font-weight: bold;
      border: none;

      &:hover {
        background-color: #cc4400;
        transition: 0.5s;
        transform: scale(1.2) translateY(1px);
      }
    }
  }
}
