@import "../Variables.scss";

.services {
  text-align: center;
  margin-top: 5rem;
  margin-bottom: 5rem;

  .heading {
    h1 {
      font-size: 3rem;
      color: $main_color;
    }

    p {
      font-size: 1.5rem;
      margin-top: 20px;
    }
  }

  .service-section {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    flex-flow: wrap;
    justify-content: center;
    justify-items: center;
    margin-top: 60px;
    margin-bottom: 60px;

    .box {
      max-width: 250px;
      width: 100%;
      height: auto;
      color: white;
      margin: 30px;

      .img-box {
        padding: 10px;

        img {
          max-width: 100px;
          width: 100%;
        }
      }

      span {
        font-size: 2.5rem;
        color: $main_color;
      }

      p {
        word-break: break-all;
        font-size: 1.5rem;
      }
    }
  }
}
