@import "../Variables.scss";

.section {
  padding: 20px 50px;

  .main {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    flex-flow: wrap;
    justify-content: space-evenly;
    margin-top: 4rem;

    .food-text {
      margin: 1.5rem;
      padding: 100px 50px 0 50px;

      h1 {
        font-size: $medium_font;
        color: $main_color;
        margin-bottom: 20px;
      }

      h2 {
        font-size: 1.5rem;
        font-weight: 400;
      }

      button {
        background-color: $main_color;
        color: $text_color;
        padding: 10px 18px;
        margin: 10px 0;
        border-radius: 4px;
        font-weight: bold;
        border: none;

        &:hover {
          background-color: #cc4400;
          transition: 0.5s;
          transform: scale(1.2) translateY(1px);
        }
      }
    }

    .burger-img {
      margin: 20px;

      img {
        max-width: 400px;
        width: 100%;
        height: auto;
      }
    }
  }
}
