@import "../Variables.scss";
.navbar {
  display: flex;
  flex-direction: row;
  background-color: $main_color;
  height: 70px;
  align-items: center;
  justify-content: space-between;
  position: relative;

  .brand {
    font-size: 2.5rem;
    cursor: pointer;
    padding: 20px;
  }
}

.fabars {
  display: none;
  position: absolute;
  top: 20px;
  right: 0;
  font-size: 28px;
  margin-right: 20px;
}

.fatimes {
  display: none;
  position: absolute;
  top: 20px;
  right: 0;
  font-size: 28px;
  margin-right: 20px;
}

.nav-links {
  margin-right: 200px;

  a {
    font-size: 1.5rem;
    color: black;
    margin-left: 60px;
    transition: all 0.5s ease-out;

    &:hover {
      color: white;
    }
  }
}

@media only screen and (max-width: 780px) {
  .nav-links {
    display: none;
  }
  .fabars {
    display: block;
    cursor: pointer;
  }
  .fatimes {
    display: block;
    cursor: pointer;
  }

  .mobile-nav-links {
    display: flex;
    flex-direction: column;
    font-size: 1.5rem;
    position: absolute;
    left: 0;
    top: 70px;
    width: 100%;
    align-items: center;
    transition: 0.5s ease-in;

    // background-color: rgb(5, 163, 111);
    background-color: #ff702a;
    a {
      color: white;
      text-align: center;
      margin: 1rem;
      width: 100%;
      width: 150px;
      padding: 0.5rem;

      &:hover {
        color: black;
        background-color: white;
        border-radius: 6px;
        transition: all 0.5s ease;
      }
    }
  }
}
