@import "../Variables.scss";

.footer {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  flex-flow: wrap;
  justify-content: center;
  margin-top: 60px;
  margin-bottom: 60px;

  .Menu-link {
    max-width: 300px;
    width: 100%;
    height: auto;
    color: white;

    h2 {
      text-decoration: underline $main_color;
      font-size: 2rem;
      margin-bottom: 20px;
    }

    ul {
      margin-bottom: 20px;

      li {
        margin: 0;
        padding: 0 0 10px 0;
        text-align: start;

        a {
          color: gray;
          font-size: 1.5rem;

          &:hover {
            color: $main_color;
          }
        }
      }
    }
  }

  .Services {
    max-width: 300px;
    width: 100%;
    height: auto;
    h2 {
      text-decoration: underline $main_color;
      font-size: 2rem;
      margin-bottom: 20px;
    }

    .ser p {
      font-size: 1.5rem;
      color: gray;
      text-align: start;
      margin: 10px 0;
      margin-bottom: 20px;

      &:hover {
        color: $main_color;
        cursor: pointer;
      }
    }
  }

  .information {
    max-width: 300px;
    width: 100%;
    height: auto;
    text-align: start;

    h2 {
      text-decoration: underline $main_color;
      font-size: 2rem;
      margin-bottom: 20px;
    }

    .info p {
      color: gray;
      font-size: 1.5rem;
      text-align: start;
      margin: 10px 0;
      margin-bottom: 20px;

      &:hover {
        color: $main_color;
        cursor: pointer;
      }
    }
  }
}

.contact {
  max-width: 300px;
  width: 100%;
  height: auto;

  h2 {
    text-decoration: underline $main_color;
    font-size: 2rem;
    margin-bottom: 20px;
  }
  div {
    i {
      font-size: 40px;
      color: $main_color;
      text-align: start;
      margin-top: 10px;
      margin-right: 24px;
      margin-bottom: 20px;

      &:hover {
        transform: scale(1.2);
        transition: 0.5s;
        color: #b7440a;
      }
    }
  }
}
