@import "../Variables.scss";

.menu-section {
  .menu-heading {
    text-align: center;
    h1 {
      color: $main_color;
      font-size: 2.5rem;
    }

    h3 {
      font-size: $small_font;
      font-weight: bold;
      margin-top: 10px;
    }
  }

  .menu-container {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    flex-flow: wrap;
    justify-content: space-evenly;
    margin-top: 60px;

    .box {
      position: relative;
      background-color: #fdd5c3;
      padding: 20px;
      border-radius: 5px;
      color: black;
      max-width: 350px;
      width: 100%;
      margin: 30px;
      text-align: center;

      .img-box {
        img {
          max-width: 250px;
          width: 100%;
        }
      }

      h1 {
        font-size: 2rem;
        font-weight: bold;
      }

      h3 {
        font-size: 1.5rem;
        font-weight: 500;
      }

      span {
        color: $main_color;
        font-weight: bold;
        font-size: 1.5rem;
      }

      .bx {
        background-color: $main_color;
        color: #fff;
        position: absolute;
        top: 0;
        right: 0;
        padding: 8px;
        font-size: 20px;
        border-bottom-left-radius: 8px;
        &:hover {
          background-color: #d25d23;
        }
      }
    }
  }
}
